/* ExpenseTracker.css */

.expense-tracker-container {
  max-width: 900px;
  margin: 0 auto;
  margin-top: 4rem;
  padding: 20px;
  border-radius: 10px;
  background-color: #bc9393; /* White background */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Soft shadow */
}

.expense-tracker-container.dark-mode {
  background-color: #333333; /* Dark background */
  color: #ffffff; /* White text on dark background */
}

.expense-tracker-container h1 {
  font-size: 24px;
  margin-bottom: 20px;
}

.expense-tracker-container h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.expense-tracker-container .complete {
  display: inline-block;
  margin-bottom: 20px;
  padding: 10px 20px;
  background-color: #4CAF50; /* Green */
  color: #ffffff;
  text-decoration: none;
  border-radius: 5px;
}

.expense-tracker-container .verify {
  display: inline-block;
  margin-bottom: 20px;
  padding: 10px 20px;
  background-color: #f44336; /* Red */
  color: #ffffff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.expense-tracker-container form {
  margin-bottom: 20px;
}

.expense-tracker-container form div {
  margin-bottom: 10px;
}

.expense-tracker-container label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.expense-tracker-container input[type="number"],
.expense-tracker-container input[type="text"],
.expense-tracker-container select {
  width: calc(100% - 10px);
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
}

.expense-tracker-container button[type="submit"],
.expense-tracker-container button[type="button"] {
  padding: 10px 20px;
  background-color: #007bff; /* Blue */
  color: #ffffff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.expense-tracker-container button[type="submit"]:hover,
.expense-tracker-container button[type="button"]:hover {
  background-color: #0056b3; /* Darker blue on hover */
}

.expense-tracker-container ul {
  list-style: none;
  padding: 0;
}

.expense-tracker-container ul li {
  margin-bottom: 10px;
}

.expense-tracker-container ul li button {
  padding: 5px 10px;
  background-color: #ff0000; /* Red */
  color: #ffffff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.expense-tracker-container ul li button:hover {
  background-color: #cc0000; /* Darker red on hover */
}
